* {
  margin: 0;
  padding: 0;
}

html,
body {
  font-family: monospace;
  font-size: 1.2rem;
}

canvas {
  width: 100vw;
  height: 100vh;
  display: block;
}

#info {
  width: 100%;
  padding: 10px;
  box-sizing: border-box;
  text-align: left;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  pointer-events: none;
  color: black;
}

